<template>
  <div>
    <PageQatarHeader/>
    <div class="timer">
      <v-container class="px-10">
        <h3>
          <img
              src="https://cloudinary.fifa.com/transform/178fcbc2-4bc8-4481-8b67-7a10895dcbfc/FIFA?tx=c_fill,g_auto,q_auto"
              width="160px"
          >
          <br>&nbsp&nbsp Qatar WORLD CUP 2022™
        </h3>
        <div class="mt-6 text-center">
          <span class="text-h6 text-sm-h4 me-1 me-sm-5">개최부터<br class="d-flex d-sm-none">&nbsp +</span>
          <span class="text-h3 text-sm-h1 font-weight-medium">{{ date }}</span>
          <span class="text-h6 text-sm-h4 me-1 me-sm-6">일</span>
          <span class="text-h3 text-sm-h1 font-weight-medium">{{ hour }}</span>
          <span class="text-h6 text-sm-h4 me-1 me-sm-6">시간</span>
          <span class="text-h3 text-sm-h1 font-weight-medium">{{ minute }}</span>
          <span class="text-h6 text-sm-h4">분</span>
        </div>
      </v-container>
    </div>
    <div id="groupList" class="pb-6">
      <v-container class="px-6">
        <h1 class="pt-3 pb-5">조 편성</h1>
        <div>
          <v-row>
            <v-col
                v-for="(group, i) in groups"
                :key="i"
                cols="12"
                md="3"
                sm="4"
            >
              <v-card
                  outlined
                  tile
                  class="pa-2 rounded"
              >
                <h2 class="group-name mx-4 my-2">{{ groups[i] }}</h2>
                <div class="px-3 mb-4">
                  <v-btn
                      v-for="(team, j) in teams[i]"
                      :key="j"
                      class="group-card mb-2 ps-1 ps-md-4 rounded justify-start"
                      block
                      depressed
                      href="#teamIntro"
                      @click="selectGroup(i), selectCountry(i, j), selectManager()"
                  >
                    <img :src="flagLink[i][j]" height="20px" width="30px" class="mx-4">
                    <h3 class="d-inline-flex font-weight-medium">{{ teams[i][j] }}</h3>
                  </v-btn>
                </div>
              </v-card>
            </v-col>
          </v-row>
        </div>
      </v-container>
    </div>
    <div id="teamIntro">
      <v-container class="px-6">
        <div class="infoText " v-if="!selectedGroup">
          상단에서 조회하고 싶은 팀을 선택하세요.
        </div>
        <div v-if="selectedGroup">
          <div>
            <h1>{{ selectedCountryName }}</h1>
            <p>{{ selectedGroup }}</p>
          </div>
          <div class="row">
            <div class="col-12 col-sm-6 text-end">
              <v-img
                  contain
                  max-height="360px"
                  :src="selectedCountryManager[0].Manager_img"
              ></v-img>
            </div>
            <div class="col-12 col-sm-6 align-self-sm-end text-start">
              <h3>감독</h3>
              <p>{{ selectedCountryManager[0].Name }}</p>
              <p>{{ selectedCountryManager[0].Birth }}</p>
            </div>
          </div>
        </div>
      </v-container>
    </div>
    <div class="playerInfo" v-if="selectedGroup">
      <v-sheet
          color="#eeeee4"
          class="mx-auto"
          max-width="1200"
      >
        <v-slide-group
            v-model="model"
            class="pa-1 pa-sm-4"
            show-arrows
        >
          <v-slide-item
              v-for="(item, index) in players.filter((i) => i.CountryCode == selectedCountryCode)"
              :key="index"
              v-slot="{ active, toggle }"
          >
            <v-card
                class="playerCard ma-4"
                height="220"
                width="140"
                @click="toggle(), selectPlayer()"
                outlined
            >
              <v-row
                  align="center"
                  justify="center"
              >
                <div class="text-center">
                  <v-img
                      contain
                      max-height="140px"
                      max-width="100px"
                      :src="item.Player_img"
                      class="mt-4 mb-1 mx-auto"
                  ></v-img>
                  <p class="pcName font-weight-bold">{{ item.name }}</p>
                  <p>{{ item.Position }}</p>
                </div>
              </v-row>
            </v-card>
          </v-slide-item>
        </v-slide-group>
        <v-expand-transition>
          <v-sheet
              color="#eeeee4"
              v-if="model != null"
              tile
          >
            <v-container>
              <v-row>
                <template class="row">
                  <div class="col-12 col-sm-4">
                    <v-img
                        contain
                        max-height="360px"
                        :src="selectedPlayer[0].Player_img"
                    ></v-img>
                  </div>
                  <div class="col-12 col-sm-8 text-start align-self-center pe-sm-16">
                    <v-simple-table dense>
                      <template v-slot:default>
                        <tbody>
                        <tr>
                          <td style="width:30%">No.{{ selectedPlayer[0].Back_No }}</td>
                          <td><h3>{{ selectedPlayer[0].name }}</h3></td>
                        </tr>
                        <tr>
                          <td>포지션</td>
                          <td><h3>{{ selectedPlayer[0].Position }}</h3></td>
                        </tr>
                        <tr>
                          <td>소속팀</td>
                          <td><h3>{{ selectedPlayer[0].club }}</h3></td>
                        </tr>
                        <tr>
                          <td>생년월일</td>
                          <td><h3>{{ selectedPlayer[0].birth }}</h3></td>
                        </tr>
                        <tr>
                          <td>출전수</td>
                          <td><h3>{{ selectedPlayer[0].games }}</h3></td>
                        </tr>
                        <tr>
                          <td>득점</td>
                          <td><h3>{{ selectedPlayer[0].goals }}</h3></td>
                        </tr>
                        </tbody>
                      </template>
                    </v-simple-table>
                  </div>
                </template>
              </v-row>
            </v-container>
          </v-sheet>
        </v-expand-transition>
      </v-sheet>
    </div>
  </div>
</template>

<script>
import PageQatarHeader from "@/components/layout/PageQatarHeader"
import dayjs from 'dayjs'
import http from "@/http-common";

export default {
  name: 'PageQatar',

  components: {
    PageQatarHeader,
    dayjs
  },

  data() {
    return {
      players: [], managers: [],

      // timer
      dateDiff: '', date: '', hour: '', minute: '',
      today: dayjs().format('YYYY-MM-DD HH:mmZ'),

      model: null,
      selectedGroup: '', selectedPlayer: '',
      selectedCountryName: '', selectedCountryCode: '', selectedCountryManager: '',
      groups: ["A조", "B조", "C조", "D조", "E조", "F조", "G조", "H조"],
      teams: [
        ["카타르", "에콰도르", "세네갈", "네덜란드"], ["잉글랜드", "이란", "미국", "웨일스"],
        ["아르헨티나", "사우디아라비아", "멕시코", "폴란드"], ["프랑스", "덴마크", "튀니지", "호주"],
        ["스페인", "독일", "일본", "코스타리카"], ["벨기에", "캐나다", "모로코", "크로아티아"],
        ["브라질", "세르비아", "스위스", "카메룬"], ["포르투갈", "가나", "우루과이", "대한민국"]
      ],
      teamsCode: [
        ["QAT", "ECU", "SEN", "NED"], ["ENG", "IRN", "USA", "WAL"],
        ["ARG", "KSA", "MEX", "POL"], ["FRA", "DEN", "TUN", "AUS"],
        ["ESP", "GER", "JPN", "CRC"], ["BEL", "CAN", "MAR", "CRO"],
        ["BRA", "SRB", "SUI", "CMR"], ["POR", "GHA", "URU", "KOR"]
      ],
      flagLink: [
        ["https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/QAT?tx=c_fill,g_auto,q_auto", "https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/ECU?tx=c_fill,g_auto,q_auto", "https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/SEN?tx=c_fill,g_auto,q_auto", "https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/NED?tx=c_fill,g_auto,q_auto"],
        ["https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/ENG?tx=c_fill,g_auto,q_auto", "https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/IRN?tx=c_fill,g_auto,q_auto", "https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/USA?tx=c_fill,g_auto,q_auto", "https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/WAL?tx=c_fill,g_auto,q_auto"],
        ["https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/ARG?tx=c_fill,g_auto,q_auto", "https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/KSA?tx=c_fill,g_auto,q_auto", "https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/MEX?tx=c_fill,g_auto,q_auto", "https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/POL?tx=c_fill,g_auto,q_auto"],
        ["https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/FRA?tx=c_fill,g_auto,q_auto", "https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/DEN?tx=c_fill,g_auto,q_auto", "https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/TUN?tx=c_fill,g_auto,q_auto", "https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/AUS?tx=c_fill,g_auto,q_auto"],
        ["https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/ESP?tx=c_fill,g_auto,q_auto", "https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/GER?tx=c_fill,g_auto,q_auto", "https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/JPN?tx=c_fill,g_auto,q_auto", "https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/CRC?tx=c_fill,g_auto,q_auto"],
        ["https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/BEL?tx=c_fill,g_auto,q_auto", "https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/CAN?tx=c_fill,g_auto,q_auto", "https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/MAR?tx=c_fill,g_auto,q_auto", "https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/CRO?tx=c_fill,g_auto,q_auto"],
        ["https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/BRA?tx=c_fill,g_auto,q_auto", "https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/SRB?tx=c_fill,g_auto,q_auto", "https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/SUI?tx=c_fill,g_auto,q_auto", "https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/CMR?tx=c_fill,g_auto,q_auto"],
        ["https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/POR?tx=c_fill,g_auto,q_auto", "https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/GHA?tx=c_fill,g_auto,q_auto", "https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/URU?tx=c_fill,g_auto,q_auto", "https://cloudinary.fifa.com/api/v3/picture/flags-sq-2/KOR?tx=c_fill,g_auto,q_auto"]
      ],
    };
  },

  mounted() {
    this.calcDate();
    this.dataPlayer();
    this.dataManager();
  },

  updated() {
    this.calcDate();
  },

  methods: {
    calcDate: function() {
      this.dateDiff = dayjs(this.today).diff('2022-11-21 01:00Z');
      this.date = dayjs(this.dateDiff).date();
      this.hour = dayjs(this.dateDiff).hour();
      this.minute = dayjs(this.dateDiff).minute();
    },
    selectGroup: function (val) {
      this.model = null;
      this.selectedGroup = this.groups[val];
    },
    selectCountry: function (i, j) {
      this.selectedCountryName = this.teams[i][j];
      this.selectedCountryCode = this.teamsCode[i][j];
    },
    selectManager: function() {
      this.selectedCountryManager = this.managers.filter((i) => i.Countrycode == this.selectedCountryCode);
    },
    selectPlayer: function() {
      this.selectedPlayer = this.players.filter((i) => i.CountryCode == this.selectedCountryCode);
      this.selectedPlayer = this.selectedPlayer.filter((_, index) => index == this.model);
      console.log(this.selectedPlayer);
    },
    dataPlayer(){
      http
          .get("/qatar/player")
          .then(response=>{
            this.players = response.data;
            console.log(response.data);
          })
          .catch(e=>{
            console.log(e);
          })
    },
    dataManager(){
      http
          .get("/qatar/manager")
          .then(response=>{
            this.managers = response.data;
            console.log(response.data);
          })
          .catch(e=>{
            console.log(e);
          })
    },
  }
}
</script>

<style lang="scss">
@import "@/assets/scss/_PageQatar.scss";
</style>